/*
 * The content reducer will always return an array of con's no matter what
 * You need to return something, so if there are no con's then just return an empty array
 * */

export default function () {
    return [
        {
            con: 1,
            text: "This is the starting point of my new cutting edge React-Redux-Sass Developers Environment complete with compilers, automated asset optimization and deployment. I have integrated several other library's as well, including Bootstrap and jQuery. So far I am just barely getting started. MongoDB will be added in as the back-end.",
            text2: "What is a redux? Redux is a predictable state container for JavaScript apps. (If you're looking for a WordPress framework, check out Redux Framework.) It helps you write applications that behave consistently, run in different environments (client, server, and native), and are easy to test.",
            text3: "What is react? React is a declarative, efficient, and flexible JavaScript library for building user interfaces. ... Your components tell React what you want to render  then React will efficiently update and render just the right components when your data changes."
        },
        {
            con: 2,
            text: "Developer Dan Weatherbee"
        },
        {
            con: 3,
            text: "Working on it :D."
        },
        {
            con: 4,
            text: "text for nav4"
        },
        {
            con: 5,
            text: "text for nav5"
        },
        {
            con: 6,
            text: "text for nav6"
        },
        {
            con: 7,
            text: "text for nav7"
        },
        {
            con: 8,
            text: "text for nav8"
        },
        {
            con: 9,
            text: "The Modern Application Stack - ReactJS, ES6 & JSX to Build a UI (the rise of MERN). In our toolbox will also be Babel, Webpack, and Mocha.",
            text2: "Mongo is installed as the Back end and CRUD Operations are setup in db.js."
        }
    ]
}



